import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { CALENDAR_CN } from '../../../../../@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { LoanHistoryBean, LoanHistoryTotalBean } from './bean/loanHistory.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as moment from 'moment';
@Component({
    selector: 'loan-history',
    templateUrl: './loan-history.component.html',
    styleUrls: ['./loan-history.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class LoanHistoryComponent implements OnInit {

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    inValue: any = 'take';//
    userform: FormGroup;
    first: number = 0;
    ch: any = CALENDAR_CN;
    msgs: Message[] = [];
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    personId: any = [];
    personName: any = [];//
    personPage: boolean = false;
    bussTypeOptions: any;//
    bussTreePage: boolean = false;//
    treeDate: any;//

    parent_value: any;
    operatingLevel: string;
    loanHistoryBean: LoanHistoryBean = new LoanHistoryBean();
    loanHistoryTotalBean: LoanHistoryTotalBean = new LoanHistoryTotalBean();
    StartDate: Date = null;
    EndDate: Date = null;
    loanTable: any = [];
    total: number;
    code: any;
    fiveTypeList: any = [];//
    loanTypeList: any = [];//
    transTypeList: any = [];//

    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';

    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;
    new_postId: any = [];
    bussListArr: any = [];
    managerName: string;//
    managerNameGlobal: any = [];
    managerId: string;//
    managerIdGlobal: any = [];//
    loanDepositValue: string;//
    endDate_new = new Date();
    startDate_new: Date;
    isShowTip: boolean = true;
    collection: any = [{}, {}, {}, {}, {}, {}];
    flag: number = 0;//
    productArr: any = ['020001', '030001', '040001', '050001'];//
    flag2: number = 1;//
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        this.userform = fb.group({
            treeOrgName: ['', Validators.required],
            StartDate: ['', Validators.required],
            EndDate: ['', Validators.required],
            loanType: [''],
            managerName: [''],
            transAmtStart: [''],
            transAmtEnd: [''],
            fiveLevelClassify: [''],
            transType: [''],
            bussTypeOptions: ['', Validators.required],
            custNum: [''],
        });
    }

    ngOnInit() {
        if (this.extParam.managePermission == 'Y') {
            this.managerName = '';
            this.managerId = '';
        } else {
            this.managerName = this.userName;
            this.managerId = this.userId;
        }

        //,
        if (this.extParam.operatingLevel == 'SUB_BRANCH_DT') {
            this.queryUpOrg();
        } else {
            //
            let dropDownSelect = this.commfunc.getSessionDataCH('dropDownSelect');
            this.treeOrgName = dropDownSelect.split(",")[0];
            this.treeOrgId = this.extParam.orgId;
            this.parent_value = this.extParam.orgId;
            this.loanDepositValue = this.extParam.orgId;
        }
        this.codeValues();
        this.fiveTypeList = this.code['FiveTypeList'];//
        this.loanTypeList = this.code['LoanTypeList'];//
        this.transTypeList = this.code['TransTypeList'];//
        //
        this.new_postId = this.code['downPostId'];

        setTimeout(() => {
            this.loanTable = [];
        });
        this.loanHistoryBean.pageNum = 1;
        this.loanHistoryBean.pageSize = 10;

        this.StartDate = this.endDate_new;
        this.EndDate = this.endDate_new;
    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
        this.parent_value = param.orgId;
        this.operatingLevel = param.operatingLevel;
    }
    perTreeShow() {
        this.orgTreePage = true;
    }
    queryFirst() {
        this.isShowTip = true;
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.flag2 = 1;
        this.loanHistoryBean.pageNum = 1;
        this.loanHistoryBean.pageSize = 10;
        this.first = 0;
        // this.query();

        let param = {
            orgId: this.treeOrgId,
            managerId: this.managerId,
            custNum: this.loanHistoryBean.custNum,
            transDateStart: this.commfunc.transDateN(this.StartDate),
            transDateEnd: this.commfunc.transDateN(this.EndDate),
            transAmtStart: this.loanHistoryBean.transAmtStart,
            transAmtEnd: this.loanHistoryBean.transAmtEnd,
            fiveLevelClassify: this.loanHistoryBean.fiveLevelClassify,
            loanType: this.loanHistoryBean.loanType,
            transType: this.loanHistoryBean.transType,
            operatingLevel: this.operatingLevel,
            businessType: this.bussListArr.join(',')
        };
        this.outValue.emit(param);
    }
    query() {
        if (this.bussTypeOptions && this.bussTypeOptions != []) {
            this.loanHistoryBean.businessType = this.bussListArr;
        }
        this.loanHistoryBean.orgId = this.treeOrgId;
        this.loanHistoryBean.managerId = this.managerId;
        this.loanHistoryBean.transDateStart = this.commfunc.transDateN(this.StartDate);
        this.loanHistoryBean.transDateEnd = this.commfunc.transDateN(this.EndDate);
        this.httpService.queryLoanHistoryDetails(this.loanHistoryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.isShowTip = false;
                this.loanTable = data.resultList;
                this.total = data.total;
                this.loanHistoryTotalBean.total = data.total;
                this.loanHistoryTotalBean.totalAmt = data.totalAmt;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    paginate(event) {
        //
        this.loanHistoryBean.pageSize = event.rows * 1;
        //
        this.loanHistoryBean.pageNum = event.page + 1;
        this.first = event.page * this.loanHistoryBean.pageSize;
        this.query();
    }
    download() {
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        let this_id = this.extParam.postId;
        let flag = true;
        for (let item of this.new_postId) {
            if (this_id == item.value) {
                flag = false;
                break
            }
        }
        if (!flag) {
            this.toDownload(flag);
        } else {
            let lcbl: any = {
                lctybl: this.extParam.postId
            }
            this.waitSelUserList = this.commfunc.query('XZZXLC', lcbl);
            if (!this.waitSelUserList.erro) {
                this.outVal = this.waitSelUserList.waitSelUserList;
                let chooseThis = this.waitSelUserList.display;
                let Header = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                let a2 = Header.extParam.specifyNextOpers;
                if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                    this.downloadPerson = true;
                } else {
                    this.toDownload(flag);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
            }
        }
    }
    toDownload(flag) {
        this.loanHistoryBean.businessType = this.bussListArr;
        this.loanHistoryBean.orgId = this.treeOrgId;
        this.loanHistoryBean.managerId = this.managerId;
        this.loanHistoryBean.transDateStart = this.commfunc.transDateN(this.StartDate);
        this.loanHistoryBean.transDateEnd = this.commfunc.transDateN(this.EndDate);
        this.httpService.loanHistoryDetailsExportFile(this.loanHistoryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: "!" });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.toDownload(true);
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //
    bussTypeShow() {
        this.bussTreePage = true;
        this.flag = 0;
    }
    //
    bussTypeCall(event) {
        this.bussTreePage = false;
        this.bussTypeOptions = [];
        this.bussListArr = [];
        console.log(event)
        for (let item of event) {
            if (this.productArr && this.productArr.length != 0) {
                for (let i = 0; i < this.productArr.length; i++) {
                    if (item.productId == this.productArr[i] || item.productId == '060001' || item.productId == '061001') {
                        if (item.productId == '060001' || item.productId == '061001') {//(\)
                            this.flag += 0.5;
                        } else {
                            this.flag += 1;
                        }
                        if (Math.ceil(this.flag) > 1) {
                            this.bussTypeOptions = [];//
                            this.bussListArr = [];
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                            return;
                        }
                        break;
                    }
                }
            }
            this.bussTypeOptions.push(item.productName);//
            this.bussListArr.push(item.productId);//
        }
    }
    //
    chectStart(date) {
        let now = moment(this.endDate_new).format('Y-MM-DD');//
        let now_date = moment(this.endDate_new).add(-1, 'day').add(-1, 'year').format('Y-MM-DD');//()
        if (date && date != null) {
            /**
             * 12
             * 12
             */
            // if (now_date > oneYearAgo) {
            //     this.EndDate = new Date();
            // } else {
            //     let end = moment(date).add(1, 'year').format('Y-MM-DD');
            //     this.EndDate = new Date(end);
            // }
            /**
             * 
             * 
             * 
             */
            if (now == this.commfunc.transDateN(date)) {
                this.EndDate = new Date(now);
            } else {
                if (this.commfunc.transDateN(date) < now_date) {
                    this.EndDate = new Date(moment(date).add(1, 'year').format('Y-MM-DD'));
                } else {
                    this.EndDate = new Date(this.endDate_new.getTime() - 24 * 60 * 60 * 1000);
                }
            }
        }
    }
    //
    chectEnd(date) {
        if (date && date != null) {
            // let start = moment(date).add(-1, 'year').format('Y-MM-DD');
            // if (this.StartDate && this.StartDate != null) {
            //     if (new Date(start) > this.StartDate) {
            //         console.log(this.EndDate);
            //         this.EndDate = null;

            //         this.msgs = [];
            //         this.msgs.push({ severity: 'error', summary: '', detail: '' });
            //     }
            //     let start = moment(date).add(-1, 'year').format('Y-MM-DD');
            //     // this.EndDate = new Date();
            //     this.StartDate = new Date(start);
            // } else {
            //     if (date.getTime() < this.StartDate.getTime()) {
            //         this.EndDate = new Date(this.StartDate);
            //     } else {
            //         this.EndDate = date;
            //     }
            // }

            let now = moment(this.endDate_new).format('Y-MM-DD');//
            if (now == this.commfunc.transDateN(date) && now != this.commfunc.transDateN(this.StartDate)) {
                this.EndDate = null;

                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
            }
        }
    }
    perShow() {
        if (this.treeOrgId == '' || !this.treeOrgId) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: ',' }];
            return;
        } else {
            this.personPage = true;
        }
    }
    perInfor(item) {
        // console.log(this.flag2)
        if (this.flag2 == 1) {//
            this.managerName = '';
            this.managerId = '';
            this.managerNameGlobal = [];
            this.managerIdGlobal = [];
        }
        this.flag2++;
        this.personPage = false;
        this.managerNameGlobal.push(item[0].tellerName);
        this.managerName = this.managerNameGlobal.join(',');
        this.managerIdGlobal.push(item[0].tellerId);
        this.managerId = this.managerIdGlobal.join(',');
    }
    //
    reset() {
        this.loanHistoryBean = new LoanHistoryBean();
        this.loanHistoryBean.pageNum = 1;
        this.loanHistoryBean.pageSize = 10;
        this.personName = '';
        this.personId = '';
        this.StartDate = new Date();
        this.EndDate = new Date();
        this.endDate_new = new Date();
        this.bussTypeOptions = [];
        this.bussListArr = [];

        ///
        if (this.extParam.operatingLevel == 'SUB_BRANCH'
            || this.extParam.operatingLevel == 'SUB_BRANCH_DT'
            || this.extParam.managePermission == 'N') {
            this.treeOrgName = this.orgName;
            this.treeOrgId = this.orgId;
        } else {
            this.treeOrgName = '';
            this.treeOrgId = '';
        }
        if (this.extParam.managePermission != 'N') {
            this.managerName = '';
            this.managerId = '';
            this.managerNameGlobal = [];
            this.managerIdGlobal = [];
        }
    }
    //
    checkMoney() {
        if (this.loanHistoryBean.transAmtStart && this.loanHistoryBean.transAmtStart != ''
            && this.loanHistoryBean.transAmtEnd && this.loanHistoryBean.transAmtEnd != '') {
            if (parseFloat(this.loanHistoryBean.transAmtStart) > parseFloat(this.loanHistoryBean.transAmtEnd)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                return
            }
        }
    }
    //
    queryUpOrg() {
        let param = {
            flag: "0",
            orgId: this.extParam.orgId,
            rowNum: "10",
            startRow: "1",
        }
        this.httpService.queryOrgByCombConditions(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.orgInfoList && data.orgInfoList.length > 0) {
                    this.treeOrgName = data.orgInfoList[0].superiorManageOrgName;
                    this.treeOrgId = data.orgInfoList[0].superiorManageOrg;
                    this.parent_value = data.orgInfoList[0].superiorManageOrg;
                    this.loanDepositValue = data.orgInfoList[0].superiorManageOrg;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
        });
    }
    //
    judgeField(id) {
        return this.commfunc.permissionCheck(id)
    }
}
